/* Here we will include mostly overrides of npm modules' styles */


body {
  margin: 0 !important; 
}

/* Scrollbar styles */

::-webkit-scrollbar {
  width: 0vh !important;
}

:focus {
  outline: 0;
}

/* react-grid-layout override */

.react-grid-item.react-grid-placeholder {
  background: #35B8C1;
}

input:-webkit-autofill, input:-webkit-autofill:active {
  -webkit-box-shadow: 0 0 0 30vh #2e3439 inset !important;
  box-shadow: 0 0 0 30vh #2e3439 inset !important;
  caret-color: white;
}

input:-webkit-autofill:focus, input:-webkit-autofill:hover {
  -webkit-box-shadow: 0 0 0 30vh #43484dff inset !important;
  box-shadow: 0 0 0 30vh #43484dff inset !important;
}

input:-webkit-autofill {
  -webkit-text-fill-color: #ffffff !important;
}

.blinking {
  animation: blinkingText 1s infinite;
}

@keyframes blinkingText {
  0% {
    background-color: #35B8C1;
    box-shadow: 0vh 0vh 12vh #35B8C1;
  }
  50% {
    background-color: #35B8C144;
    box-shadow: 0vh 0vh 4vh #35B8C122;
  }
  100% {
    background-color: #35B8C1;
    box-shadow: 0vh 0vh 12vh #35B8C1;
  }
}




.appear {
  animation: appearAnimation 3s 1;
}

@keyframes appearAnimation {
  0% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}







svg>g:last-child>g:last-child {
  pointer-events: none
}

div.google-visualization-tooltip {
  pointer-events: none
}

@keyframes slideInFromLeft {
  0% {
    opacity: 1;
  }
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.enter-animation {
  /* This section calls the slideInFromLeft animation we defined above */
  animation: 1s slideInFromLeft;
  animation-iteration-count: 2;
}

