/************************* NEW LOADER ***********************/
 

    
.container {
  text-align: center;
}




.loader {
  position: relative;
  width: 80vh;
  height: 60vh;
  border-radius: 50%;
  display: inline-block;
  vertical-align: middle;
  margin-top: 50%;
}

.loader-section {
  width: 30%;
  display: inline-block;
  text-align: center;
  min-height: 215vh;
  vertical-align: top;
  margin: 1%;
  /* background: #090a0b; */
}


/*LOADER-7*/

.loader-7 .line {
  width: 8vh;
  position: absolute;
  border-radius: 5vh;
  bottom: 0;
  /* background: linear-gradient(to bottom, #2F3337, #121416); */
